// SET_TEXT_FILTER
export const setTextFilter = (text = "") => ({
  type: "SET_TEXT_FILTER",
  text: text,
});
// SORT_BY_DATE
export const sortByDate = () => ({ type: "SORT_BY_DATE" });
// SORT_BY_AMOUNT
export const sortByAmount = () => ({ type: "SORT_BY_AMOUNT" });
// SET_START_DATE
export const setStartDate = (tico = undefined) => ({
  type: "SET_START_DATE",
  startDate: tico,
});
// SET_END_DATE
export const setEndDate = (mochie = undefined) => ({
  type: "SET_END_DATE",
  endDate: mochie,
});
